#root {
  max-width: 100%;
  min-height: 100vh;
  margin: 0 auto;
}

.hero {
  min-height: 100vh;
  width: 100%;
  margin: 0 auto;
  text-align: center;
  display: flex;
  justify-content: space-between;
  color: var(--clr-light);
  align-items: center;
  background: rgb(36, 36, 36);
  background: linear-gradient(156deg, rgba(36, 36, 36, 1) 0%, rgba(20, 20, 94, 1) 54%, rgba(4, 121, 145, 1) 100%);
  text-shadow: 1px 1px 2px var(--clr-dark);
}

.section {
  min-height: 80vh;
  padding: 7em 2em;
}

.block-1 {
  padding: 1em;
}

.block-2 {
  height: 332px;
  width: 60%;
  display: none;

}

@media (min-width: 600px) {

  .block-1 {
    padding: 3em 5em 1em;
  }

  .block-2 {
    width: 20%;
    display: block;
    padding: 0;
  }

  .social-media-icon {
    width: 30px;

  }
}

@media (max-width: 400px) {
  body {
    font-size: 1rem;
  }
}

.logo {
  height: 10em;
  padding: 1em;
}

.logo:hover {
  filter: drop-shadow(0 0 2em #b4b8f9aa);
}


@keyframes logo-flip {
  0% {
    transform: rotateY(0deg);
  }

  100% {
    transform: rotateY(360deg);
  }
}

@keyframes gradient {
  0% {
    background-position: 50% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 50% 50%;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .hero {
    background: url("./images/wave-lines.svg"), linear-gradient(-45deg, #0b749a, #1d43ab, #12627f, #12558c);
    background-size: 600% 100%;
    animation: gradient 55s ease infinite;
  }

  .logo,
  .nav--logo {
    animation: logo-flip 1.75s ease-in;
  }


}

.title {
  font-size: 3.2em;
  line-height: 1.1;
  margin: 0.5em 0 0.2em;
}

.page-title {
  font-family: 'Work Sans', sans-serif;
  font-weight: 300;
}

.job-title {
  font-size: 0.7em;
}

.hero-body {
  width: 90%;
  max-width: 450px;
  font-size: 1.2em;
  padding: 0 1em;
  margin: 0 auto;
  letter-spacing: 1px;
}



/* ========== HERO ========== */
.social-media {
  padding: 1em 0;
}

.social-media-list {
  width: 50%;
  padding: 0;
  margin: 0 auto;
  list-style: none;
  display: flex;
  justify-content: space-between;
}

.social-media-icon {
  width: 20px;

}

/* ========================= Utility =========================  */

.on-hover {
  filter: invert(31%) sepia(87%) saturate(1309%) hue-rotate(130deg) brightness(106%) contrast(103%);
}

.highlight {
  color: var(--clr-highlight);
}

.not-found {
  min-height: 100vh;
  padding: 2em;
  text-align: center;
  display: flex;
  place-content: center;
  align-items: center;
}

.not-found>div {
  height: fit-content;
}

/* ====================== PORTFOLIO ====================== */

.portfolio {
  padding: 5em 1.5em;
  font-family: 'Work Sans', sans-serif;
  font-weight: 300;
}

.project {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.project-flex-container{
  display: flex;
  flex-direction: column;
}

.project-info-container,
.project-image-container {
  width: 90%;
  max-width: 35em;
  align-self: center;
}

.project-title {
  font-size: 2.5em;
  line-height: 1.2;
  position: relative;
  display: inline-block;
}

.project-title::after {
  content: "";
  width: 100%;
  height: 15px;
  background-color: var(--clr-highlight);
  display: block;
  position: absolute;
  top: 90%;
  z-index: -1;
}

.stack-container {
  margin: 2em 0;
}

.stack {
  background-color: var(--clr-dark);
  color: var(--clr-light);
  padding: 0.3em 1.2em;
  font-size: 1.2em;
  font-weight: 700;
  border-radius: 4px;
  display: inline-block;
  margin: 1em 0.75em 0 0;
  letter-spacing: 1px;
}

.project-description {
  width: 90%;
  font-size: 1.5em;
  margin: 0.5em 0.001em 1.5em 0;
}


.project-image {
  width: 100%;
  border-radius: 12px;
  margin: 0 auto;
}

.project-image:hover,
.project-image:focus {
  -webkit-box-shadow: 14px 22px 11px 3px rgba(123, 118, 118, 0.75);
  -moz-box-shadow: 14px 22px 11px 3px rgba(123, 118, 118, 0.75);
  box-shadow: 14px 22px 11px 3px rgba(123, 118, 118, 0.75);
  transition: box-shadow 400ms ease-in-out;
}


/* ====================== ABOUT ====================== */
.about-flex-container{
  display: flex;
  flex-direction: column-reverse;
}
.about-body{
  margin-bottom: 1.5em;
  max-width: 550px;
}

.cv-text{
  font-size: 1.5em;
  padding: 1em 0;
}

.about-highlight a{
  color: var(--clr-dark);
  font-weight: 700;
  cursor: pointer;
  text-decoration: none;
}

.about-highlight a:active,
.about-highlight a:hover{
  color: var(--clr-dark);
  text-decoration: underline currentColor;
}

.profile-image{
  width: 40%;
}
/* ====================== BLOG ====================== */


/* ====================== CONTACT ====================== */

@media (min-width: 800px) {
  .project-title::after {
    width: 50%;
    top: 70%;
    left: -30px;
  }

  .project-flex-container{

    flex-direction: row;
    justify-content: space-around;
  }

  .project-info-container,
  .project-image-container {
    width: 47.5%;
  }

  .about-flex-container{
    flex-direction: row;
  }

  .about-body-container{
    width: 60%;
  }

  .cv-text{
    width: 40%;
  }
}